.register {
  width: 100vw;
  height: 100vh;
  background: linear-gradient(to bottom, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 1) 100%), url("https://cdn.hipwallpaper.com/i/98/21/dUyCkp.jpg");
  background-size: cover;
  position: relative;
}
.register .top .wrapper {
  padding: 20px 50px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.register .top .wrapper .logo {
  height: 40px;
}
.register .top .wrapper .loginButton {
  width: 100px;
  background-color: red;
  border: none;
  color: white;
  border-radius: 5px;
  padding: 5px 15px;
  font-size: 16px;
  font-weight: 500;
  cursor: pointer;
}
.register .container {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  color: white;
}
.register .container h1 {
  font-size: 50px;
  overflow: hidden;
}
.register .container h2 {
  margin: 20px;
  overflow: hidden;
}
.register .container p {
  font-size: 20px;
  overflow: hidden;
}
.register .container .input {
  width: 50%;
  background-color: white;
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-top: 20px;
  height: 50px;
  border-radius: 5px;
  overflow: hidden;
}
.register .container .input input {
  width: 100%;
  height: 100%;
  border: none;
  padding: 0 10px;
}
.register .container .input .registerButton {
  width: 25%;
  height: 100%;
  background-color: red;
  border: none;
  color: white;
  font-size: 22px;
  cursor: pointer;
  margin: 10px -7px;
}
