.list{
    background-color: rgb(0, 0, 0);
    color: white;
    width: 100%;
    padding-top: 10px;
    height: 100%;
}

.title{
    font-size: 20px;
    font-weight: 50px;
    margin-left: 68px;
}
.wrapper {
    position: relative;
}
    .left svg{
      width: 50px;
      height: 100%;
      background-color: rgb(22, 22, 22, 0.5);
      color: white;
      position: absolute;
      z-index: 99;
      top: 0;
      bottom: 0;
      margin: auto;
      cursor: pointer;
      left:0px;
    }
    .right svg{
        width: 50px;
      height: 100%;
      background-color: rgb(22, 22, 22, 0.5);
      color: white;
      position: absolute;
      z-index: 99;
      top: 0;
      bottom: 0;
      margin: auto;
      cursor: pointer;
      right:0px;
    }


.containerlist{
    margin-left: 50px;
    display: flex;
    margin-top: 10px;
    overflow: hidden;
    transform: translateX(0px);
    transition: all 1s ease;
}

