.login {
    width: 100vw;
    height: 100vh;
    background: linear-gradient(to bottom, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 1) 100%), url("http://mppmduse2pmpovwapp.azurewebsites.net/wp-content/uploads/2019/09/netflix-background-9.jpg");
    background-size: cover;
    position: relative;
}
 .wrapper {
    padding: 20px 50px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    overflow: hidden;
}
h1{
    padding-bottom: 10px;
    padding-left: 60px;
}
 .logo {
    height: 40px;
}
 .container {
    width: 100%;
    height: 100%;
    padding-right: 80px;
    position: absolute;
    top: 0;
    left: 0;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    color: white;
    overflow: hidden;
}
 form {
    width: 350px;
    height: 400px;
    padding: 30px;
    border-radius: 5px;
    background-color: var(--main-color);
    display: flex;
    flex-direction: column;
    justify-content: space-around;
}
input {
    width: 285px;
    height: 40px;
    border-radius: 5px;
    background-color: gray;
    color: white;
    padding-left: 10px;
}
 input::placeholder {
    color: lightgray;
}
 button {
    height: 40px;
    width:50%;
    margin: 10px 60px;
    border-radius: 5px;
    background-color: red;
    color: white;
    border: none;
    font-size: 18px;
    font-weight: 500;
    cursor: pointer;
}
 span {
     padding: 3px 40px;
    color: lightgray;
}
 span b {
    color: white;
}
