.feature{
   position: relative;
   height: 100%;
}
.feature .category {
    position: absolute;
    top: 80px;
    left: 50px;
    font-size: 30px;
    font-weight: 500;
    color: white;
    display: flex;
    align-items: center;
}
.feature .category select {
    cursor: pointer;
    background-color: var(--main-color);
    border: 1px solid white;
    color: white;
    margin-left: 20px;
    padding: 5px;
}
.feature #genre {
    z-index: 2;
}

.info{
    position: absolute;
    width:35%;
    left:70px;
    bottom: 220px;
    color: white;
    padding-left: 10px;
}

.info img{
    width:40vh;
}

.desc{
    margin: 20px 0px;
}

.buttons{
    display: flex;
}

button{
    padding: 8px 16px;
    border: none;
    border-radius: 5px;
    font-size: 15px;
    font-weight: 400;
    margin-right: 10px;
    cursor: pointer;
    justify-content: center;
    align-items: center;
}

.play{
    color: black;
    background-color: white;
}
   span{
     
    padding: 6px;
 } 

.more{
    color: white;
    background-color: darkgrey;
}